import localforage from 'localforage';
import { Category, BaseType } from 'src/lib/api';

const FAV_STORAGE_PREFIX = 'favorite-';

type AllowedFavoritesType = Exclude<Category, 'section' | 'literature'>;
type isFavouriteType = (
  id: number,
  type: AllowedFavoritesType
) => () => Promise<boolean>;
const isFavourite: isFavouriteType = (id, type) => {
  return async (): Promise<boolean> => {
    const items = await localforage.getItem(`${FAV_STORAGE_PREFIX}${type}`);
    if (items) {
      return (
        ((items as unknown) as Array<number>).find((i) => i === id) !==
        undefined
      );
    }
    return false;
  };
};

type SetFavoriteType = (
  id: number,
  status: boolean,
  type: AllowedFavoritesType
) => Promise<boolean>;
const setFavourite: SetFavoriteType = async (id, status, type) => {
  const instanceName = `${FAV_STORAGE_PREFIX}${type}`;
  let items: Array<number> = await localforage.getItem(instanceName);
  items = items ?? [];
  if (status) {
    items = [...items, id];
  } else {
    const index = items.findIndex((v) => v === id);
    items = [...items.slice(0, index), ...items.slice(index + 1)];
  }
  await localforage.setItem(instanceName, items);
  return true;
};

