type GetFavoritesReturnType = Record<AllowedFavoritesType, BaseType[]>;
type GetFavoritesType = () => GetFavoritesReturnType;
const getFavorites: GetFavoritesType = () => {
  const favorites: GetFavoritesReturnType = {
    chapter: [], //getFavoritesByType('chapter'),
    kural: [], //getFavoritesByType('kural'),
  };
  return favorites;
};
type useFavoritesType = () => [
  isFavouriteType,
  SetFavoriteType,
  GetFavoritesType
];
export const useFavorites: useFavoritesType = () => {
  return [isFavourite, setFavourite, getFavorites];
};
