import React from 'react';

import { makeStyles, Badge, IconButton, BadgeOrigin } from '@material-ui/core';
import ListItem from '@material-ui/core/ListItem';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import clsx from 'clsx';
import { Kural } from 'src/lib/api';
import { useNavigation } from 'src/lib/hooks';

import KuralDetail from './KuralDetail';

const useStyles = makeStyles((theme) => ({
  listItem: {
    marginBottom: theme.spacing(1),
    [theme.breakpoints.up('sm')]: {
      cursor: 'pointer',
    },
  },
  kuralListItem: {
    flexDirection: 'row',
    alignItems: 'start',
    justifyContent: 'space-between',
  },
  badgeContainer: {
    position: 'absolute',
  },
  kuralBadge: {
    opacity: 0.8,
    height: '1.5em',
    fontSize: '0.5em',
    top: theme.spacing(-1),
    left: 2,
  },
  kuralWrapper: {
    width: '100%',
  },
  kuralDetailAction: {
    position: 'absolute',
    right: '-4px',
    opacity: 0.3,
  },
  detailBar: {},
  kuralActionContainer: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  kuralAction: {
    borderRadius: 'unset',
    padding: 0,
    width: '1em',
    height: '1em',
  },
}));

type KuralCardProps = {
  kural: Kural;
  className?: string;
  showBadgeRight?: boolean;
};
const KuralCard: React.FC<KuralCardProps> = (props: KuralCardProps) => {
  const classes = useStyles();
  const [navigateTo] = useNavigation();
  const [showDetail, toggleDetail] = React.useState(false);
  const [first, second] = props.kural.value.split('|');
  const badgePosition: BadgeOrigin = props.showBadgeRight
    ? { vertical: 'bottom', horizontal: 'right' }
    : { vertical: 'top', horizontal: 'left' };
  return (
    <>
      <ListItem
        className={props.className}
        divider={!showDetail}
        classes={{ root: clsx(classes.listItem, classes.kuralListItem) }}>
        <Badge
          color='primary'
          badgeContent={props.kural.id}
          anchorOrigin={badgePosition}
          classes={{
            badge: classes.kuralBadge,
            root: classes.badgeContainer,
          }}
          className='kural-badge'
          max={1330}
        />
        <div
          onClick={navigateTo(`/list/kural/${props.kural.id}`)}
          className={classes.kuralWrapper}>
          <div className='first'>{first}</div>
          <div className='second'>{second}</div>
        </div>
        {showDetail && (
          <IconButton
            className={classes.kuralDetailAction}
            size='small'
            onClick={(): void => toggleDetail(!showDetail)}>
            <ArrowForwardIosIcon />
          </IconButton>
        )}
      </ListItem>

      {showDetail && (
        <ListItem divider={true} classes={{ root: clsx(classes.listItem) }}>
          <KuralDetail kural={props.kural.id} />
          {/* <div className={classes.detailBar}>
            <div className={classes.kuralActionContainer}>
              <IconButton size='small' classes={{ root: classes.kuralAction }}>
                <PlayCircleFilledIcon fontSize='small' color='primary' />
              </IconButton>
              <IconButton size='small' classes={{ root: classes.kuralAction }}>
                <InfoIcon fontSize='small' color='primary' />
              </IconButton>
            </div>
          </div> */}
        </ListItem>
      )}
    </>
  );
};

export default KuralCard;
