import React from 'react';

import { Container, makeStyles, Chip } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { useRoute, useNavigation } from 'src/lib/hooks';
import { setTitle, setDefaultTitle } from 'src/state/title';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    margin: 0,
  },
  chipRoot: {
    minWidth: '15vw',
    fontSize: '0.7em',
    height: '1.5em',
    margin: `0 1vw 4px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type RouteViewerProps = {};

const RouteViewer: React.FC<RouteViewerProps> = (props: RouteViewerProps) => {
  const classes = useStyles(props);
  const [navigateTo] = useNavigation();
  const dispatch = useDispatch();
  const [, routes] = useRoute({});
  React.useEffect(() => {
    const { type, value } = routes[routes.length - 1];
    dispatch(type === 'kural' ? setDefaultTitle() : setTitle(value));
  }, [routes, dispatch]);

  return (
    <Container>
      {routes.map((item, index) => (
        <Chip
          size='small'
          label={item.value}
          color={'primary'}
          onClick={(): void => {
            if (item.route) {
              navigateTo(item.route)();
            }
          }}
          style={{ backgroundColor: item.color }}
          classes={{ root: classes.chipRoot }}
          key={index}
        />
      ))}
    </Container>
  );
};

export default RouteViewer;
