import React from 'react';

import { useParams } from 'react-router-dom';
import SwipeableViews from 'react-swipeable-views';
import { mod } from 'react-swipeable-views-core';
import { virtualize } from 'react-swipeable-views-utils';
import { getItemByPosition, isCategoryValid } from 'src/lib/api';
import { useNavigation } from 'src/lib/hooks';

import RouteViewer from './../../components/kural/RouteViewer';
import { ListKuralsOfChapter } from './ListKuralsOfChapter';

const VirtualizeSwipeableViews = virtualize(SwipeableViews);

type KuralsByChapterViewerProps = {
  className?: string;
};

const KuralsByChapterViewer: React.FC<KuralsByChapterViewerProps> = (
  props: KuralsByChapterViewerProps
) => {
  const { chapter } = useParams<{ chapter: string }>();
  const chapterNo = Number(chapter);
  const [navigateTo] = useNavigation();
  return (
    <>
      {/* <RouteViewer /> */}
      <div className={props.className}>
        <VirtualizeSwipeableViews
          enableMouseEvents
          overscanSlideBefore={chapterNo === 1 ? 0 : 1}
          overscanSlideAfter={chapterNo === 133 ? 0 : 1}
          index={chapterNo - 1}
          onChangeIndex={(index, indexLatest): void => {
            const chap = parseInt(chapter);
            navigateTo(
              `/list/${getItemByPosition(
                'chapter',
                index - indexLatest < 0 ? 'PREV' : 'NEXT',
                chap
              )}/kural`
            )();
          }}
          slideRenderer={({ key, index }): React.ReactNode => {
            const nextIndex = mod(index, 133) + 1;
            return isCategoryValid('chapter', nextIndex) ? (
              <ListKuralsOfChapter key={key} chapterNo={nextIndex} />
            ) : null;
          }}
        />
      </div>
    </>
  );
};

export default KuralsByChapterViewer;
