import React from 'react';

import { IconButton } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

const useStyles = makeStyles((theme) => ({
  icon: {
    fill: 'white',
  },
}));

type FavouriteToggleProps = {
  className?: string;
  active?: () => Promise<boolean>;
  handler: (isActive: boolean) => Promise<boolean>;
};

const FavouriteToggle: React.FC<FavouriteToggleProps> = (
  props: FavouriteToggleProps
) => {
  const classes = useStyles();
  const [isActive, toggleActive] = React.useState<boolean>(false);
  React.useEffect(() => {
    if (props.active) {
      props.active().then((isActive) => {
        toggleActive(isActive);
      });
    }
  });
  const toggle = (): void => {
    props.handler(!isActive).then((result) => {
      if (result) {
        toggleActive(!isActive);
      }
    });
  };
  return (
    <IconButton size='small' onClick={toggle} className={props.className}>
      {isActive && <FavoriteIcon className={classes.icon} />}
      {!isActive && <FavoriteBorderIcon className={classes.icon} />}
    </IconButton>
  );
};

export default FavouriteToggle;
