import React from 'react';

import { useSelector } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';

import { AppState } from '../../state/reducers';
import FavouriteToggle from '../../views/components/common/FavouriteToggle';
import { useFavorites } from './useFavorites';

type useKuralHeaderType = () => [string, React.ReactNode];
export const useKuralHeader: useKuralHeaderType = () => {
  const [isFavourite, setFavourite] = useFavorites();
  const title = useSelector((st: AppState) => st.title);
  const chapterRoute = useRouteMatch<{ chapter: string }>(
    '/list/:chapter/kural'
  );
  const kuralRoute = useRouteMatch<{ kural: string }>('/list/kural/:kural');
  let actions = null;
  if (chapterRoute?.isExact) {
    const chapterNo = parseInt(chapterRoute.params.chapter);
    actions = (
      <FavouriteToggle
        active={isFavourite(chapterNo, 'chapter')}
        handler={(isActive: boolean): Promise<boolean> =>
          setFavourite(chapterNo, isActive, 'chapter')
        }
      />
    );
  } else if (kuralRoute?.isExact) {
    const kuralNo = parseInt(kuralRoute.params.kural);
    actions = (
      <FavouriteToggle
        active={isFavourite(kuralNo, 'kural')}
        handler={(isActive: boolean): Promise<boolean> =>
          setFavourite(kuralNo, isActive, 'kural')
        }
      />
    );
  }
  return [title, actions];
};
