import React from 'react';

import { makeStyles, ListItem, Chip, List } from '@material-ui/core';
import { Kural } from 'src/lib/api';
import { useKuralDetail } from 'src/lib/hooks';

import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  root: {
    cursor: 'grab',
  },
  kural: {
    borderBottom: 'unset',
    color: 'brown',
    fontWeight: 600,
    '& .kural-badge': {
      display: 'none',
    },
  },
  transliteration: {
    flexDirection: 'column',
    alignItems: 'self-end',
    '& .transliterator-name': {
      fontSize: '0.7em',
      height: '1.5em',
    },
    '& .content': {
      fontSize: '0.9em',
      padding: '0.4em',
      textAlign: 'justify',
      WebkitTextStrokeWidth: '0.2px',
      WebkitTextStroke: 'thin',
      letterSpacing: 1,
    },
  },
}));

type KuralDetailProps = {
  className?: string;
  kural: Kural | number;
};

const KuralDetail: React.FC<KuralDetailProps> = (props: KuralDetailProps) => {
  const classes = useStyles();
  const [kural, kuralDetail, transliterators] = useKuralDetail(props.kural);
  return (
    <List
      className={props.className}
      classes={{ root: classes.root }}
      {...props}>
      {kural && <KuralCard kural={kural} className={classes.kural}></KuralCard>}
      {kuralDetail &&
        transliterators.map(({ id: transliteratorId, name }) => (
          <ListItem key={transliteratorId} className={classes.transliteration}>
            <Chip
              size='small'
              label={name}
              color={'primary'}
              className={'transliterator-name'}
            />
            <div className={'content'}>{kuralDetail[transliteratorId]}</div>
          </ListItem>
        ))}
    </List>
  );
};

export default KuralDetail;
